import { apiRequest } from "@/config/axios";

// Start Match
export function createMatch(lobbyId, matchDetails) {
    return new Promise((resolve, reject) => {
        try {
            if(!lobbyId) return reject("No Lobby Id found");

            apiRequest.post(`/lobbies/${lobbyId}/matches/create`, matchDetails)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

export function getMatch(lobbyId, matchId) {
    return new Promise((resolve, reject) => {
        try {
            if(!lobbyId) return reject("No lobbyId found");
            if(!matchId) return reject("No matchId found");

            apiRequest.get(`/lobbies/${lobbyId}/matches/${matchId}`)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

export function updateBid(lobbyId, matchId, data) {
    return new Promise((resolve, reject) => {
        try {
            if(!lobbyId) return reject("No lobbyId found");
            if(!matchId) return reject("No matchId found");
            if(!data) return reject("No nextPlayer found");

            apiRequest.patch(`/lobbies/${lobbyId}/matches/${matchId}/bid`, data)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

