<template>
    <div>
        <div class="mb-1">
            <button @click="startMatch()" style="width: 100%" class="btn btn-primary">Start Match</button>
        </div>
        <div class="d-flex flex-wrap justify-content-around">
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                phase - {{matchData?.phase}}
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                Number of decks - {{matchData?.numberOfDecks}}
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                Bid points - {{matchData?.bidPoints}}
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                Partners - {{matchData?.partners}}
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                maxAlloBidPoint - {{maxAlloBidPoint}}
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                Players - {{matchData?.players}}
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                <b>activePlayer - {{matchData?.activeTurn}}</b>
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                <b>trumpCard - {{matchData?.partnerCardType}}</b>
            </div>
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                <b>Max Partners - {{maxPartnerCards}}</b>
            </div>
        </div>
        <div class="mb-2">
            Trump Cards
            <div>
                <PlayerCards :cards="matchData?.partnerCards" class="d-flex">
                    <template #card="{data}">
                        <div class="position-re">
                            <div class="position-ab bg-white mb-10px box-shadow-2 border-radius-8-px p-5px" style="top: 5px; right: 5px;">
                                {{data?.occurence}}
                            </div>
                            <img
                                :src="require(`@/assets/images/cards/${data.fileName}`)" 
                                class="card-image"
                                style="margin: 0;"
                                @click="togglePartnerCard(data)"
                            />
                        </div>
                    </template>
                </PlayerCards>
            </div>
        </div>
        <template v-if="phase === 1">
            <!-- BID WAR -->
            <div class="bg-white mb-10px box-shadow-2 border-radius-8-px p-1">
                Bid value
                <template v-if="myTurn && maxAlloBidPoint > 0">
                    <div class="d-flex">
                        <select v-model="bidValue" class="mr-5px">
                            <option disabled hidden :value="null">Select</option>
                            <option v-if="(maxAlloBidPoint > 0)" value="pass">Pass</option>
                            <option v-if="(maxAlloBidPoint > 5)" :value="5">5</option>
                            <option v-if="(maxAlloBidPoint > 10)" :value="10">10</option>
                            <option v-if="(maxAlloBidPoint > 15)" :value="15">15</option>
                        </select>
                        <button class="btn outline-secondary" @click="updateBidFun()">Bid</button>
                    </div>
                </template>
            </div>
        </template>
        <template v-if="phase === 2">
            <!-- SELECT TRUMP -->
            <Modal
                :closeOnBackdrop="false"
                :header="false"
                acceptButtonText="Confirm"
                :cancelButton="false"
                v-model="showTrumpSelection"
                className="selection-modal"
                @accept="handleSelectTrump()"
                @close="showTrumpSelection = false"
            >
                <template #body>
                    <div class="d-flex justify-content-center flex-column align-items-center overflow-x-auto" style="width: 100%;">
                        <strong class="mb-10px font-size-22">Select your Trump card</strong>
                        <PlayerCards
                            :cards="cards.filter((x) => x?.number === 1)"
                            :stack="false"
                            class="d-flex pb-5px mb-10px"
                            style="max-width: 100%; gap: 10px;"
                        >
                            <template #card="{data}">
                                <img
                                    :src="require(`@/assets/images/cards/${data.fileName}`)" 
                                    class="card-image"
                                    :class="{'card-disabled': trumpCard !== data.type}"
                                    @click="trumpCard = data?.type"
                                />
                            </template>
                        </PlayerCards>
                    </div>
                </template>
            </Modal>

            <!-- SELECT PARTNER CARDS -->
            <Modal
                :closeOnBackdrop="false"
                :header="false"
                acceptButtonText="Confirm"
                :cancelButton="false"
                v-model="showPartnerSelectionModal"
                className="selection-modal"
                @accept="setTrumpAndPartnersFun()"
                @close="showPartnerSelectionModal = false"
            >
                <template #body>
                    <div class="d-flex justify-content-evenly" style="height: 100%; width: calc(100% - 5px);">
                        <div class="d-flex flex-column align-items-center overflow-auto" style="width: 49%;">
                            <strong class="mb-10px font-size-22">Select your partner cards</strong>
                            <PlayerCards
                                :cards="availableCards.filter((x) => partnerCards.filter((y) => x.type === y.type && y.number === x.number)?.length < matchData?.numberOfDecks)"
                                :stack="false"
                                class="overflow-y-auto border-radius-8-px bg-light-gray d-flex justify-content-evenly flex-wrap style-scroll p-10px mb-10px"
                                style="max-width: 100%; gap: 10px; height: 100%; width: calc(100% - 20px)"
                            >
                                <template #card="{data}">
                                    <DropDown :zIndex="9" title="Occurence selection" v-if="matchData?.numberOfDecks > 1 && partnerCards.length < maxPartnerCards">
                                        <template #button>
                                            <button :ref="`card-selection${data.number}${data.deck}`">DD</button>
                                        </template>
                                        <template #options>
                                            <DropDownOption v-for="i in matchData?.numberOfDecks" :key="i" @click="partnerCards.push({...data, occurence: i}), $refs[`card-selection${data.number}${data.deck}`].click()">
                                                {{i}}
                                            </DropDownOption>
                                        </template>
                                    </DropDown>
                                    <img
                                        :src="require(`@/assets/images/cards/${data.fileName}`)" 
                                        class="card-image"
                                        :class="{'card-disabled': partnerCards.length >= maxPartnerCards}"
                                        @click="matchData?.numberOfDecks > 1 ? '' : togglePartnerCard({...data, occurence: 1})"
                                    />
                                </template>
                            </PlayerCards>
                        </div>
                        <div class="d-flex flex-column align-items-center overflow-auto" style="width: 49%;">
                            <strong class="mb-10px font-size-22">Selected cards</strong>
                            <PlayerCards
                                :cards="partnerCards"
                                :stack="false"
                                class="overflow-y-auto d-flex flex-wrap style-scroll justify-content-evenly border-radius-8-px bg-light-gray p-10px mb-10px"
                                style="max-width: 100%; gap: 10px; height: 100%; width: calc(100% - 20px);"
                            >
                                <template #card="{data}">
                                    {{data?.occurence}}
                                    <img
                                        :src="require(`@/assets/images/cards/${data.fileName}`)" 
                                        class="card-image"
                                        @click="togglePartnerCard(data)"
                                    />
                                </template>
                            </PlayerCards>
                        </div>
                    </div>
                </template>
            </Modal>
        </template>
        <template v-if="phase === 3">
            <!-- PLAYED CARDS -->
            <hr/>
            <label>Played Hand Cards {{matchData?.playedCards?.length}}</label>
            <div class="mt-10px">
                <PlayerCards :cards="matchData?.playedCards?.[matchData?.playedCards.length-1]?.cards"/>
            </div>
        </template>

        <hr/>
        <label>Your Cards - WON POINTS: {{myPoints}}</label>
        <!-- PLAYER CARDS -->
        <div class="mt-10px">
            <PlayerCards :cards="matchData?.perPlayerCards?.[userId]">
                <template #card="{data}">
                    <img
                        :src="require(`@/assets/images/cards/${data.fileName}`)" 
                        class="card-image"
                        :class="{'card-disabled': activeTurn !== userId || !canBePlayed(data)}"
                        @click="(phase === 3 && activeTurn === userId && canBePlayed(data)) ? playCardFun(data) : ''"
                    />
                </template>
            </PlayerCards>
        </div>

        <!-- SHOW WINNER -->
            <Modal
                :closeOnBackdrop="false"
                :header="false"
                :footer="false"
                acceptButtonText="Back to Lobby"
                :cancelButton="false"
                v-model="showWinner"
                className="winner-modal"
                @close="showWinner = false"
                @accept="$router.replace({name: `Lobby`, params: {id: $route.params.id}})"
            >
                <template #body>
                    <div class="d-flex justify-content-center flex-column align-items-center" style="width: 100%;">
                        <strong class="mb-10px font-size-22">You {{matchData?.winner === "Partners" ? (matchData.partners.includes(userId) ? "Won": "Lost") : (!matchData.partners.includes(userId) ? "Won"  : "Lost")}}</strong>
                        {{winnerMessage}}

                        <p style="color: blue;">You will be automatically redirected to lobby</p>
                    </div>
                </template>
            </Modal>
    </div>
</template>

<script setup>
// COMPONENTS
import PlayerCards from "@/components/atom/PlayedCards/PlayedCards.vue";
import Modal from "@/components/atom/Modal/Modal.vue";
import DropDown from "@/components/atom/DropDown/DropDown.vue";
import DropDownOption from "@/components/atom/DropDownOption/DropDownOption.vue";

// PACKAGES
import { dbCollections } from "@/config/collections";
import { getMatch, playCard, setTrumpAndPartners, updateBid } from "@/helpers/match";
import { mongodbSnapshot } from "@/utils/realm/crudOperationsWithSanpshot/crudOperationsWithSanpshot";
import { defineComponent, inject, onBeforeUnmount, onMounted, ref, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
import { useToast } from "vue-toast-notification";
import { broadcast } from "@/socket";
import { BSON } from "realm-web";
import cards from "@/config/cards.json";
import { apiRequest } from "@/config/axios";

// HELPERS
const userId = inject("userId");
const route = useRoute();
const router = useRouter();
const $toast = useToast();

defineComponent({
    name: "Match-Component"
})

const matchData = ref({});
const myPoints = ref(0);
const maxPartnerCards = ref(0);
const matchSnap = ref({});
const activeTurn = ref("");
const nextPlayer = ref({});
const phase = ref(1);
const availableCards = ref([]);
const trumpCard = ref();
const partnerCards = ref([]);
const showPartnerSelectionModal = ref(false);
const showTrumpSelection = ref(false);
const showWinner = ref(false);
const winnerMessage = ref("");

const myTurn = ref(false);
const maxAlloBidPoint = ref(0);
const bidValue = ref(null);
const inProcess = ref(false)

onMounted(() => {
    checkValidMatch()
    .then((valid) => {
        if(valid) {
            init();
        } else {
            router.replace({name: "Lobby", params: {id: route.params.id}});
        }
    })
    .catch((error) => {
        console.error("ERROR in check match: ", error);
        $toast.error("Invalild match", {position: "top-right"});
        router.replace({name: "Lobby", params: {id: route.params.id}});
    })
})

onBeforeUnmount(() => {
    if(matchSnap.value?.return) {
        matchSnap.value?.return();
    }
})

watch(matchData, () => {
    if(matchData.value?.completed) {
        $toast.success("Match completed", {position: "top-right"});
        setTimeout(() => {
            router.replace({name: "Lobby", params: {id: route.params.id}});
        }, 30000);
    }

    activeTurn.value = matchData.value?.activeTurn;
    myTurn.value = activeTurn.value === userId.value;
    phase.value = matchData.value?.phase;

    if(phase.value === 4) {
        showWinner.value = true;
        winnerMessage.value = `${matchData.value?.winner} won with ${matchData.value?.winPoints} points!`
    }

    myPoints.value = matchData.value?.points?.[userId.value] || 0;

    availableCards.value = Object.entries(matchData.value.perPlayerCards || {}).reduce((acc, next) => {
        const [key, value] = next
        if(key !== userId.value) {
            return [...acc, ...value]
        } else {
            return acc
        }
    }, [])
    availableCards.value = availableCards.value.filter((x) => x.number !== 1);
    const aces = cards.filter((x) => x.number === 1);
    availableCards.value = [...aces, ...availableCards.value];

    maxPartnerCards.value = Math.floor(matchData.value?.players.length/2) - 1

    if(myTurn.value) {
        nextPlayer.value = getNextPlayer();
    }
    maxAlloBidPoint.value = matchData.value.maxPoints - matchData.value.bidPoints;
})

function checkValidMatch() {
    return new Promise((resolve, reject) => {
        try {
            getMatch(route.params?.id, route.params.mid)
            .then((res) => {
                if(res?.data) {
                    if(res.data?.completed) {
                        $toast.error("Match already completed", {position: "top-right"})
                        return resolve(false)
                    }
                    if(res.data?.players.includes(userId.value)) {
                        matchData.value = res.data;
                        if(matchData.value.phase === 2 && matchData.value.bidBy === userId.value) {
                            showTrumpSelection.value = true
                        }
                    }
                    resolve(res.data);
                } else {
                    reject("No data found");
                }
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error)
        }
    })
}

function fetchMatch() {
    const options = {
        subCollection: dbCollections.MATCHES,
        watchFilter: {
            filter: {
                $or: [
                    {
                        "operationType": {$in: ["delete"]}
                    },
                    {
                        "operationType": {$in: ["update", "insert", "replace"]},
                        "fullDocument._id": BSON.ObjectId(route.params.mid)
                    }
                ]
            }
        }
    }
    mongodbSnapshot(options, ({data, type, error, snap}) => {
        if(error) {
            console.error("ERROR in watch lobby: ", error);
        } else {
            matchSnap.value = snap;
            if(type === "insert") {
                matchData.value = data.fullDocument;
            } else if (type === "update" || type === "replace") {
                matchData.value = data.fullDocument;

                if(matchData.value?.phase === 2) {
                    if(matchData.value.bidBy === userId.value) {
                        showTrumpSelection.value = true
                    } else {
                        $toast.success("Partner card selection begun", {position: "top-right"});
                    }
                }
            } else if (type === "delete") {
                router.replace({name: "Lobby", params: {id: route.params.id}});
                snap?.return();
            }
        }
    })
}

function init() {
    try {
        fetchMatch();
    } catch (error) {
        console.error("ERROR: ", error);
    }
}

function getNextPlayer() {
    let playerIndex = matchData.value?.players?.findIndex((x) => x === userId.value)
    let player = ""
    if(playerIndex !== -1) {
        if(playerIndex < matchData.value?.players?.length - 1) {
            player = matchData.value?.players[playerIndex]
        } else {
            player = matchData.value?.players[0]
        }
    }
    return player
}

function updateBidFun() {
    if(inProcess.value) {
        return;
    }
    inProcess.value = true;

    const updateData = {
        bidPoint: bidValue.value,
        playerId: userId.value
    }
    broadcast(route.params.mid, {
        phase: 1,
        bidValue: bidValue.value
    })

    bidValue.value = null;

    updateBid(route.params.id, route.params.mid, updateData)
    .then(() => {
        inProcess.value = false
    })
    .catch((error) => {
        console.error("ERROR in play card:", error);
        inProcess.value = false
    })
}

function togglePartnerCard(card) {
    const cardIndex = partnerCards.value?.findIndex((x) => x.type === card.type && x.number === card.number);

    if(cardIndex !== -1) {
        partnerCards.value.splice(cardIndex, 1);
    } else {
        if(maxPartnerCards.value > partnerCards.value?.length) {
            partnerCards.value.push({...card});
        }
    }
}

function handleSelectTrump() {
    if(!trumpCard.value) {
        $toast.error("Please select a trump card", {position: "top-right"});
        return;
    }
    showPartnerSelectionModal.value = true;
    showTrumpSelection.value = false;
}

function setTrumpAndPartnersFun() {
    if(partnerCards.value?.length < maxPartnerCards.value) {
        $toast.error("Please select partner cards", {position: "top-right"});
        return;
    }

    if(inProcess.value) {
        return;
    }
    inProcess.value = true;

    showPartnerSelectionModal.value = false
    const updateData = {
        partnerCardType: trumpCard.value,
        partnerCards: partnerCards.value?.map((x) => {
            delete x.deck;
            x.occured=0;

            return x;
        }),
        activeTurn: userId.value,
        phase: 3,
        $push: {
            partners: userId.value
        }
    }

    setTrumpAndPartners(route.params.id, route.params.mid, updateData)
    .then(() => {
        inProcess.value = false
    })
    .catch((error) => {
        console.error("ERROR in play card:", error);
        inProcess.value = false
    })
}

function playCardFun(data) {
    if(inProcess.value) {
        return;
    }
    inProcess.value = true;

    const play = matchData.value.currentPlay;
    const cardData = JSON.parse(JSON.stringify(data))
    delete cardData.fileName;

    const updateData = {
        playerId: userId.value,
        play: play,
        card: cardData,
        firstPlay: !matchData.value.playedCards?.[play-1]?.cards?.length
    }

    playCard(route.params.id, route.params.mid, updateData)
    .then(() => {
        inProcess.value = false;
    })
    .catch((error) => {
        console.error("ERROR in play card:", error);
        inProcess.value = false;
    })
}

function startMatch() {
    if(inProcess.value) {
        return;
    }
    inProcess.value = true;

    let data = {
        "players": matchData.value?.players,
        "activePlayer": activeTurn.value,
        "phase": matchData.value?.phase === 1 ? 1 : 3,
        "partnerCardType": matchData.value?.partnerCardType,
        "partnerCards": matchData.value?.partnerCards
    };

    apiRequest.post(`/lobbies/${route.params.id}/match/${route.params.mid}/start`, data)
    .then(() => {
        inProcess.value = false;
    })
    .catch((error) => {
        console.error(error);
        inProcess.value = false;
    });
}

function canBePlayed(card) {
    try {
        let canPlay = false;
        if(matchData.value?.playedCards[matchData.value?.currentPlay - 1]) {
            let handCardType = matchData.value?.playedCards[matchData.value?.playedCards?.length-1]?.cards?.[0]?.type;

            if(matchData.value?.perPlayerCards?.[userId.value]?.some((x) => x.type === handCardType)) {
                canPlay = handCardType === card.type;
            } else {
                canPlay = true;
            }
        } else {
            canPlay = true;
        }

        return canPlay;
    } catch (error) {
        console.error("ERROR: ", error);
        return false;
    }
}
</script>

<style lang="css">
.selection-modal {
    width: 80vw;
}
.selection-modal .modal-body {
    width: calc(100% - 15px);
    height: 80vh;
}
.winner-modal .modal-body {
    width: 300px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-disabled {
    filter: blur(1px) brightness(0.9);
    box-shadow: none !important;
}
</style>